<template>
  <v-container grid-list-xs>
    <v-row>
      <v-col cols="12">
        <v-card class="mx-auto" max-width="500">
          <v-card-title primary-title>
            <v-icon class="mr-3">fa-exclamation-triangle</v-icon>Articulo no encontrado
          </v-card-title>
          <v-card-text class="py-0 pb-1">
            El articulo {{$route.params.id}} no encontrado
            <v-divider class="mt-5"></v-divider>
            <div class="overline py-1 text-center">
              <v-icon class="mr-3">{{msg_e.icon}}</v-icon>
              "{{msg_e.msg}}"
            </div>
            <v-divider class="mb-2"></v-divider>
          </v-card-text>
          <v-card-actions>
            <v-btn color="warning" text @click="reloadList">¿reintentar?</v-btn>
            <v-btn color="info" text @click="volver">Volver</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-overlay :value="loading" opacity=".92">
      <v-col class="d-flex flex-column justify-center align-center">
        <v-progress-circular indeterminate color="blue" size="64" width="5"></v-progress-circular>
        <div class="white--text overline text-center mt-5">cargando</div>
      </v-col>
    </v-overlay>
  </v-container>
</template>

<script>
import { mapState } from "vuex";

export default {
  computed: {
    ...mapState(["msg_e"]),
    ...mapState("products", ["producto"]),
  },
  methods: {
    async reloadList() {
      this.loading = true;
      await this.$store.dispatch("products/getProducts");
      setTimeout(() => {
        this.$store.commit("products/showProduct", this.$route.params.id);
        this.loading = false;
      }, 1500);
    },
    volver() {
      this.$router.go(-1);
    }
  },
  data: () => ({
    loading: false
  }),
};
</script>