<template>
  <div v-if="producto">
    <v-card :ripple="false" flat max-width="900" class="mx-auto pa-5 mt-5" outlined>
      <v-row no-gutters>
        <v-col cols="12" md="6" align="center">
          <v-img :src="getIMG" style="opacity: 0.8" max-width="500" class="align-self-center">
            <v-btn
              absolute
              dark
              fab
              right
              top
              text
              class="mt-8"
              :color="getColor"
              @click.stop="favorito"
            >
              <v-icon>fa-heart</v-icon>
            </v-btn>
          </v-img>
        </v-col>
        <v-spacer></v-spacer>
        <v-col cols="12" md="5">
          <v-card-title>{{producto.name}}</v-card-title>
          <v-card-subtitle>{{producto.departamento}} | {{producto.categoria}}</v-card-subtitle>

          <v-card-text>
            <v-row no-gutters align="center" class="mx-0">
              <v-col>
                <h1 class="text-left px-0 pt-2 pb-5">
                  {{formatMoney(producto.precio)}}
                </h1>
              </v-col>
              <v-col cols="12">
                <v-rating
                  :value="getRate"
                  background-color="blue lighten-4"
                  dense
                  half-increments
                  readonly
                  size="14"
                ></v-rating>
                <div class="grey--text">Existencia: {{producto.existencia }}</div>
                <div class="grey--text">Referencia: {{$route.params.id}}</div>
              </v-col>
              <v-col cols="12">{{producto.descripcion}}</v-col>
            </v-row>
          </v-card-text>
          <v-card-actions>
            <v-row no-gutters>
              <v-col cols="12">
                <v-select
                  :items="[1,2,3,4,5,6,'más de 6']"
                  v-model="cantidad"
                  label="Cantidad"
                  hide-details
                  class="mx-1 mt-1 pb-2"
                  dense
                  flat
                  outlined
                />
              </v-col>
              <v-col cols="12">
                <v-btn color="info" block>
                  <v-icon class="mr-5">fa-shopping-cart</v-icon>Comprar ahora
                </v-btn>
              </v-col>
              <v-col cols="12" >
                <v-btn color="blue" block outlined class="mt-1" @click="agregar">
                  <v-icon class="mr-5">fa-buy</v-icon>Agregar al carrito
                </v-btn>
              </v-col>
            </v-row>
          </v-card-actions>
        </v-col>
      </v-row>
    </v-card>
  </div>
  <div v-else>
    <error-page />
  </div>
</template>

<script>
import { mapState } from "vuex";
import ErrorPage from "@/components/errorPage";

export default {
  async created() {
    // console.log(this.$route.params.id)
    await this.$store.commit("products/showProduct", this.$route.params.id);
    this.$store.commit("navigate", false);
    if (!this.producto) {
      await this.$store.commit("getMSGERROR");
    }
  },
  computed: {
    ...mapState(["msg_e"]),
    ...mapState("products", ["producto"]),
    getIMG() {
      return this.producto.img
        ? require(`@/assets/products/${this.producto.img}`)
        : require(`@/assets/logo.png`);
    },
    getRate() {
      return this.producto.name == "PATATA MADRE" ? 5 : Math.random() * 5;
    },
    getColor() {
      if (this.producto.selected != undefined) {
        return this.producto.selected ? "red darken-1" : "grey";
      }
      return "grey";
    }
  },
  data: () => ({
    loading: false,
    cantidad: 0
  }),
  components: {
    ErrorPage
  },
  methods: {
    favorito() {
      if (this.producto.selected) {
        this.$store.dispatch("favoritos/eliminarFavorito", {
          id: this.$store.state.auth.usuario.token,
          product: {_id: this.productID},
        });
      } else {
        this.$store.dispatch("favoritos/addFavorito", {
          id: this.$store.state.auth.usuario.token,
          product: this.producto._id,
        });
      }
      this.producto.selected = !this.producto.selected;
    },
    agregar(){
      this.$store.dispatch('shopping_cart/agregarAlCarrito', {
        producto: this.producto._id,
        cantidad: this.cantidad,
        precio: this.producto.precio,
      })
    },
    formatMoney(mount) {
      return (mount).toLocaleString('en-US', {
        style: 'currency',
        currency: 'MXN',
      })
    }
  }
};
</script>